.courseInfo{
    height: 100%;
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}

.dark-courseInfo *{
    color: rgba(255,255,255,1);
}

.title{
    padding-top: 2.5rem;
}

input:disabled{
    color: rgba(127.5,127.5,127.5,1);
}

.ciInput{
    background: transparent;
    border: 0;
    border-bottom: 1px;
    outline: 0;
    height: 2.375rem;
}

.ciSubmit{
    /*background: transparent;*/
    border-radius: 2em;
    outline: 0;
    color: rgba(0,0,0,0.65);
}

.dark-courseInfo .ciSubmit{
    color: rgba(250,250,250,1);
}

.ciSubmit:focus{
    outline: 0;
}